.cv-header {
  box-shadow: 2px 0px 7px rgba(1, 1, 1, 0.5);
  box-sizing: border-box;
  height: 100%;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-right: 3.5px solid transparent;
  border-image: linear-gradient(
    150deg,
    #a3ff6e,
    #f0ff6e,
    #ffd86e,
    #ffa36e,
    #b7f8db,
    #50a7c2,
    #00aeff,
    #f184ff,
    #ff70e7,
    #ff9ebb,
    #9effdf,
    #9effab
  );
  border-image-slice: 1;
}

.cv-header__image > * {
  margin: 10px 0 20px 0;
  border-radius: 100px;
  box-shadow: 0px 0px 5px 1px rgba(1, 1, 1, 0.5);
  transition: all 0.3s ease-in;
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite;
}

.cv-header__image > *:hover {
  transform: scale(1.04);
  box-shadow: 0px 0px 5px 2px rgba(1, 1, 1, 0.5);
  -webkit-animation: pulse 1s infinite;
          animation: pulse 1s infinite;
}

.cv-header__image > *:active {
  transform: scale(0.99);
  box-shadow: 0px 0px 2px 1px rgba(1, 1, 1, 0.5);
}

@-webkit-keyframes pulse {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
  }

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
  }

  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}

@keyframes pulse {
  0% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7);
  }

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 10px rgba(0, 0, 0, 0);
  }

  100% {
    transform: scale(0.95);
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}

